<template>
  <div class="main">
    <section class="final-group">
        <!--<figure class="share">
        </figure>-->
        <section class="output">
            <img :src="finalImg" class="final-img-block">
            <section class="turn-final" @click="turnFinal"></section>
        </section>
        <h5 class="success-tip">长按海报保存并分享</h5>
        <figure class="success"></figure>
    </section>
  </div>

</template>

<script>
export default {
  name: 'forth',
  data () {
    return {
      per: 0,
      finalImg:this.$store.state.finalImg
    }
  },
  methods:{
      turn2Next(){
          this.isClicked=true;
        setTimeout(()=>{
            this.$router.push({name:'second'})
        },1000)
      },
      turnFinal(){
          window.location.href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU2MzA3NTQ5OQ==&scene=124#wechat_redirect";
      }
  },
  mounted(){
      var inter=setInterval(()=>{
          this.per+=10;
          if(this.per==100){
            clearInterval(inter)
          }
      },120)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>



