<template>
  <div class="main">
    <section class="fly-group">
        <figure class="plane">
        </figure>
        <section class="loading">
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="per">{{per+'%'}}</span>
        </section>
        <section class="loading-msg">
            制作中
        </section>
    </section>
    <output-photo style="position:absolute;top:100%"></output-photo>

  </div>

</template>

<script>
import outputPhoto from './outputPhoto'

export default {
  name: 'third',
  data () {
    return {
      per: 0
    }
  },
  components:{
      outputPhoto
  },
  methods:{
      turn2Next(){
          this.isClicked=true;
        setTimeout(()=>{
            this.$router.push({name:'second'})
        },1000)
      }
  },
  mounted(){
      var inter=setInterval(()=>{
          this.per+=10;
          if(this.per==100){
            clearInterval(inter)
            this.$router.push({name:'forth'})
          }
      },120)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>



