<template>
  <div class="main">
    <section class="coin-group">
        <figure class="coin coin-1">
        </figure>
        <figure class="coin coin-1">
        </figure>
        <figure class="coin coin-1">
        </figure>
    </section>
    <figure class="logo"></figure>
    <section class="main-title">
    </section>
    <section class="packet">
        <figure class="head"></figure>
        <figure class="money" :class="{'toggle-up':isClicked}">

        </figure>
        <figure class="hand">
            <figure class="btn" @click="turn2Next">
            </figure>
        </figure>
    </section>
  </div>

</template>

<script>
export default {
  name: 'first',
  data () {
    return {
      isClicked: false
    }
  },
  methods:{
      turn2Next(){
          this.isClicked=true;
        setTimeout(()=>{
            this.$router.push({name:'second'})
        },1000)
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>



