<template>
  <div style="width:100%;height:100%">
    <div class="final" id="final">
        <img :src="imgUrl" alt="" class="final-real">
        <img src="../assets/final-base.png" alt="" class="final-base">
        <img src="../assets/id.jpg" alt="" class="id-img">
        <p class="final-note">{{note}}</p>
    </div>
    <img v-if="finalImg" :src="finalImg" alt="" class="final-img">
      
  </div>

</template>

<script>
import html2canvas from 'html2canvas'
export default {
  name: 'outputPhoto',
  data () {
    return {
      imgUrl:this.$store.state.imgUrl,
      note:this.$store.state.note,
      finalImg:''
    }
  },
  methods:{
      turn2Next(){
          console.log(this.$store)
      }
  },
  mounted(){
      var self=this;
    html2canvas(document.querySelector('#final'), {allowTaint: false, taintTest: false}).
    then(function (canvas) {
        var imgUri = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream"); // 获取生成的图片的url  
        self.$set(self,'finalImg',imgUri);
        self.$set(self.$store.state,'finalImg',imgUri);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>


