import Vue from 'vue'
import Router from 'vue-router'
import first from '@/modules/firstPage'
import second from '@/modules/secondPage'
import third from '@/modules/thirdPage'
import forth from '@/modules/forthPage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/first',
      name: 'first',
      component: first
    },
    {
      path: '/second',
      name: 'second',
      component: second
    },
    {
      path: '/third',
      name: 'third',
      component: third
    },
    {
      path: '/forth',
      name: 'forth',
      component: forth
    },
    {
      path: '/', 
      redirect: '/first'
    },
  ]
})



