<template>
  <div class="main" id="main">
    <section class="coin-group">
        <figure class="coin coin-1">
        </figure>
        <figure class="coin coin-1">
        </figure>
        <figure class="coin coin-1">
        </figure>
    </section>
    <figure class="logo"></figure>
    <section class="enp-group">
        <figure class="photo-group">
          <figure class="photo">

          </figure>
          <img ref="preview" :style="{backgroundImage:'url('+imgUrl+')'}" class="photo-real">
          <figure class="btn" :class="{'upload-btn':!imgUrl,'change-btn':imgUrl}"></figure>
        </figure>
        <section class="enp-group">
          <figure class="paper">
          <input class="img-btn btn" type="file" id="uploadImg" name="uploadImg" @change="upload" ref="file">
          </figure>
          <textarea cols="30" rows="10" class="txt" v-model="note" placeholder="是点击输入宝贝红包的故事"></textarea>
          <figure class="enp-cover">
            <figure class="output-btn" @click="output"></figure>
          </figure>
        </section>
    </section>
  </div>

</template>

<script>
import html2canvas from 'html2canvas'
export default {
  name: 'first',
  data () {
    return {
      isClicked: false,
      imgUrl:'',
      imgUrl2:'',
      fileData:'',
      note:''
    }
  },
  methods:{
      turn2Next(){
         this.$router.push({name:'third'})
      },
      upload(){
        var checkImg=new RegExp("(.jpg$)|(.png$)|(.bmp$)|(.jpeg$)","i");
        if(checkImg.test(this.$refs.file.value)){
            this.previewImg();
        }else{
            alert("只支持上传.jpg .png .bmp .jpeg;你的选择有误");
        }
      },
      previewImg(){
        var reader=new FileReader();
        var self=this;
        reader.readAsDataURL(this.$refs.file.files[0]);
        reader.onload=function(e){
          self.$set(self,'imgUrl',reader.result)
        }
          
      },
      output(){
        this.$store.state.imgUrl=this.imgUrl;
        this.$store.state.note=this.note.substr(0,70);
        this.$router.push({name:'third'})
        // var self=this;
        // let width=Number(document.getElementById('main').style.width.replace('px',''));
        // let height=Number(document.getElementById('main').style.height.replace('px',''));
        // html2canvas(document.querySelector('#main'), {allowTaint: false, taintTest: false}).
        // then(function (canvas) {
        //   var imgUri = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream"); // 获取生成的图片的url  
        //   window.location.href= imgUri; // 下载图片
        // });
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>



